import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SelectionnerFilmPage } from './selectionner-film.page';

const routes: Routes = [
	{
		path: '',
		component: SelectionnerFilmPage
	},
	{
		path: 'ajouter-film',
		loadChildren: () => import('./ajouter-film/ajouter-film.module').then((m) => m.AjouterFilmPageModule)
	}
];

@NgModule({
	imports: [ RouterModule.forChild(routes) ],
	exports: [ RouterModule ]
})
export class SelectionnerFilmPageRoutingModule {}
