import { Component, OnInit, OnDestroy } from '@angular/core';
import { Film } from 'src/app/services/interfaces/film';
import { DataFilmService } from 'src/app/services/data-film.service';
import { NavController } from '@ionic/angular';
import { Subscription } from 'rxjs';

@Component({
	selector: 'app-selectionner-film',
	templateUrl: './selectionner-film.page.html',
	styleUrls: [ './selectionner-film.page.scss' ]
})
export class SelectionnerFilmPage implements OnInit, OnDestroy {
	searchInput: string;
	films: Film[];

	private filmSub: Subscription;

	constructor(private serviceFilm: DataFilmService, private navCtrl: NavController) {
		this.films = [ ...serviceFilm.filmNonVu ];
	}

	ngOnInit() {
		this.filmSub = this.serviceFilm.filmNonVuChanged.subscribe((film) => {
			this.films = film;
		});
	}

	ngOnDestroy() {
		this.filmSub.unsubscribe();
	}

	goToAjouter() {
		this.navCtrl.navigateForward([ '/selectionner-film/ajouter-film' ]);
	}

	goBack() {
		this.navCtrl.navigateBack([ '/medias/films' ]);
	}

	showInfo(id: number) {
		this.navCtrl.navigateForward([ '/info-add-film/' + id ]);
	}
}
